import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@mdi/font/css/materialdesignicons.css'

/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faUserXmark,faUserSecret,faFilePdf, faFileExcel,faCircleArrowLeft,faHouse,faMagnifyingGlass,faPen, faTrash, faCircleInfo, faEye,faArrowLeft } from '@fortawesome/free-solid-svg-icons'

library.add(faUserXmark,faUserSecret,faFilePdf,faFileExcel,faCircleArrowLeft,faHouse,faMagnifyingGlass,faPen,faTrash,faCircleInfo,faEye,faArrowLeft)

Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
